.ToolBar {
  position: absolute;
  width: 100%;
  height: 40px;
  background-color: rgb(41, 41, 41);
  border-bottom: 1px solid rgb(255, 255, 255);
  box-sizing: border-box;
}

.ToolBar .tools {
  position: relative;
  top: 50%;
  padding: 0px;
  margin: 0px 0px 0px 10px;
  transform: translateY(-50%);
}

.ToolBar .tools li {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-right: 1px;
}

.ToolBar .tools li:hover {
  background-color: rgb(95, 95, 95);
}

.ToolBar .tools li.selected {
  background-color: rgb(95, 95, 95);
}

.ToolBar .tools li img {
  display: inline-block;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  vertical-align: center;
  user-select: none;
}

.ToolBar .tools li.disabled img {
  opacity: 0.3;
}

.ToolBar .buttons {
  position: absolute;
  padding: 0px;
  margin: 0px;
  left: 280px;
  top: 0px;
  height: 100%;
  line-height: 38px;
  color: white;
  font-size: 14px;
}

.Sequence {
  position: absolute;
  left: 0px;
  top: 40px;
  bottom: 0px;
  width: 100%;
  background-color: black;
  overflow: auto;
}

.Sequence .area {
  position: relative;
  pointer-events: none;
}

.LaneComponent {
  position: absolute;
  height: 100%;
}

.lane {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0);
}

.lane-sc {
  background-color: rgb(66, 22, 28);
}

.lane-w {
  background-color: rgb(39, 39, 39);
}

.lane-b {
  background-color: rgb(13, 19, 41);
}

.lane-separater {
  background-color: rgb(0, 0, 0);
}

.lane-label {
  position: sticky;
  top: 2px;
  color: rgba(255, 255, 255, 0.625);
  text-shadow:
    0px 0px 8px rgb(0, 0, 0),
    0px 0px 8px rgb(0, 0, 0),
    0px 0px 8px rgb(0, 0, 0),
    0px 0px 8px rgb(0, 0, 0),
    0px 0px 8px rgb(0, 0, 0),
    0px 0px 8px rgb(0, 0, 0),
    0px 0px 8px rgb(0, 0, 0),
    0px 0px 8px rgb(0, 0, 0);
  font-size: 15px;
  font-weight: bold;
  writing-mode: vertical-rl;
  z-index: 5;
  user-select: none;
}

.lane-label.selected {
  color: rgb(255, 255, 255);
}

.grid-h {
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: rgb(255, 255, 255);
}

.grid-measure {
  background-color: rgb(255, 255, 255);
}

.grid-sub {
  background-color: rgba(255, 255, 255, 0.5);
}

.grid-edit {
  background-color: rgba(255, 255, 255, 0.25);
}

.grid-v {
  position: absolute;
  width: 1px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.5);
}

.NoteComponent {
  position: absolute;
  transform: translateY(-100%);
  background-color: rgb(255, 61, 61);
  width: 27px;
  height: 10px;
  outline: 1px solid rgba(255, 255, 255, 0.5);
  outline-offset: -1px;
}

.NoteComponent .text {
  position: absolute;
  top: -4px;
  color: rgba(255, 255, 255, 0.875);
  text-shadow: 1px 1px 0px rgb(0, 0, 0);
  font-size: 11px;
  font-weight: bold;
  white-space: nowrap;
  user-select: none;
}

.NoteComponent .text.selected {
  color: rgb(255, 255, 255);
}

.CursorNote {
  position: absolute;
  transform: translateY(-100%);
  width: 27px;
  height: 10px;
  outline: 2px solid rgba(255, 255, 255);
  outline-offset: -2px;
}
